import { React, useEffect,useState } from "react";
import axios from "axios";
import { saveAs } from "file-saver";
import { FileUploader } from "react-drag-drop-files";
import fleche from "../images/icons8-chevron-droit-64.png";
import downloadIcon from "../images/icons8-downloading-updates-64.png";
import textIcon from "../images/icons8-google-traduction-64.png";
import contractIcon from "../images/smart-contract.png";


//const API= "http://localhost:8080/"

const API = process.env.API_URL 

var y = "";

function About() {
  const [clicked, setClicked] = useState(false);
  const [canDownload, setCanDownload] = useState(false);
  const createContract = () => {
    setClicked(true);
    setCanDownload(false)
  };


  const fileTypes = ["sol"];
  const UploadContract = (File) => {
    const data = new FormData();
    data.append("file", File);
    axios.post("uploadContract", data).then((res) => {
      // then print response status
      console.log(res.data);
      //createContract(res.data)
      y = res.data;
      console.log(y);
      setCanDownload(true)
    });
  };
// click "download" button => save i the device
  if (clicked === true) {
    const blob = new Blob([y], { type: "text/plain;charset=utf-8" });
    saveAs(blob, "result/output.sol");
  }

  const [file, setFile] = useState(null);
  const handleChange = (file) => {
    // console.log(file)
    setFile(file);
    UploadContract(file);
  };
  const chidrenElem = (
    <>
      <div className="flex flex-col justify-center  items-center pt-5 pb-6  cursor-pointer h-96 Ml:h-36 Ml:pb-0 md:items-center  sm:w-full ">
        <svg
          aria-hidden="true"
          className="mb-3 w-10 h-10 place-self-center text-gray-400"
          fill="none"
          stroke="currentColor"
          viewBox="0 0 24 24"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            stroke-linecap="round"
            stroke-linejoin="round"
            stroke-width="2"
            d="M7 16a4 4 0 01-.88-7.903A5 5 0 1115.9 6L16 6a5 5 0 011 9.9M15 13l-3-3m0 0l-3 3m3-3v12"
          ></path>
        </svg>
        <p className="mb-2 md:ml-4 text-sm text-gray-500 dark:text-gray-400">
          <span className="font-semibold">Click to upload</span> or drag and
          drop your file
        </p>
        <p className="text-xs text-gray-500 dark:text-gray-400">(.sol)</p>
        <br />
        {file == null ? "" : file.name}
      </div>
    </>
  );
  return (
    <>
      
      <div className="my-10 text-center"> Deploy your smart contract on Ethereum blockchain in full security, Download your contract address and the ABI json file as a result</div>
      <div className=" Ml:m-auto Ml:mt-8  panel Ml:h-96 border-2 border-sky-600 relative  ">
      <div className=" flex Ml:inline-block title right-2 ">
         <div className="w-3/6 flex lg:w-full Ml:pb-0 mt-1 Ml:my-0 ml-4 Ml:ml-6">
          <img src={textIcon} alt="" className=" h-14 sm:h-6" />
          <div className=" md:text-sm md:m-1 text-xl text-bold text-sky-700 m-3 ">
            Smart contract
          </div>
        </div>
        <div className="w-1/6 "></div>
          <div className="w-3/6 h-1/6 mt-3 Ml:mt-0 flex md:w-full Ml:absolute Ml:bottom-28  Ml:left-6">
          <img src={contractIcon} alt="" className="  h-10 sm:h-5  " />
          <div className=" md:text-sm ml-4 mt-1 text-xl text-bold  text-sky-700  panelchild ">
          Deployment result
          </div>
          </div>

        </div>

        <div className=" w-full mt-4 sm:mt-0 sm:mb-0 flex Ml:inline-block h-5/6  p-1 ">
<div className=" w-3/5 w-full Ml:mt-0 Ml:w-full static rounded-3xl   border-2 border-sky-600 ">
    
      <>
      <FileUploader
        maxSize="4"
        children={chidrenElem}
        classes="justify-center items-center w-full"
        handleChange={handleChange}
        name="file"
        types={fileTypes}
      /></>
      </div>
       <img className=" Ml:rotate-90 Ml:my-0  Ml:mx-auto place-self-center h-8 w-8" src={fleche} />

<div className=" w-3/5 w-full Ml:w-full Ml:mt-4 static rounded-3xl   border-2 border-sky-600 ">
  <div className="h-flex  Ml:h-36  h-fit relative  ">
   
     
      <>{!canDownload && 
       
        <p className=" flex w-full flex-col  h-80 Ml:h-28  justify-center items-center  text-md "></p>}
        </>
        
  
    
      <>{canDownload && 
       
        <p className=" flex w-full flex-col  h-80  Ml:h-28  justify-center items-center text-center  text-md sm:text-sm "> <button onClick={createContract} ><img src={downloadIcon} alt="" className=" " /></button>Your json file is ready to download!! </p>}
        </>
        
    
  <div className=" w-flex flex absolute relative">
   
    
      <button
        onClick={createContract}
        className="absolute right-8 sm:right-18   flex sm:inline-block Ml:flex  w-28  Ml:w-24 sm:w-20 sm:text-sm   Ml:h-6  sm:h-9   sm:pl-0 pl-2   rounded border border-dashed border-sky-600  "
      >
        <img src={downloadIcon} alt="" className=" w-5 sm:w-4 sm:ml-8 Ml:ml-2 " /> Download
      </button>
   
   
    
   
    
  </div>
  </div>
</div>
   
</div>
      </div>
    </>
  );
}
export default About;
